import React from 'react';
import styles from "./Contactunder.module.css"

function Contactunder() {
  return (
    <div className={styles.container}>
      <h1 style={{ fontSize: '3em', color: 'black' }}>석/박사과정 대학원생 모집</h1>

      <p>본 연구실의 연구 분야에 관심이 있는 대학원 석/박사과정 연구원을 모집합니다</p>
      <h2>모집 분야</h2>
      <p>본 연구실은 인공지능, 딥러닝, 기계학습 등을 연구합니다</p>
      <p> 빅데이터 시대를 맞이하여 관련 업계 및 연구소에 진출을 희망하는 분들은 대학원 진학을 추천합니다.</p>

      <h2>지원 혜택</h2>
      <ul className={styles.sibal}>
        <li>프로젝트 인건비 지급 가능</li>
        <li>국내/외 학회 및 교육 참가</li>
      </ul>

      <h2>상담 신청</h2>
      <p>대학원 진학 또는 연구실 지원을 고민하고 있거나, 현재 진행중인 연구분야에 대해 궁금한 사항을 가지고 있는 학생은 언제든지 상담을 환영합니다</p>
      <p>이메일: hnvlab@</p>

      <div className={styles.box}>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <div>
            <h2 className={styles.address}>Address</h2>
            <img src="img/206.jpg" width="500" alt="사진" />
          </div>
          <div>
            <h3>Human and Vision Lab</h3>
            <h4>서울특별시 화랑로 815 삼육대학교 제1실습관 206호</h4>
            <h4>815, Hwarang-ro, Nowon-gu, Seoul, 01795 Rep. of KOREA sahmyook university</h4>
          </div>
        </div>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <div>
            <p>삼육대학교 213호</p>
            <img src="img/213.jpg" width="500" alt="사진" />
          </div>
        </div>
        <h2 className={styles.address}>Server room</h2>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <img src="img/server1.jpg" width="500" alt="사진" />
        </div>
      </div>
    </div>
  );
}

export default Contactunder;
