import React, { useState } from 'react';
import './Gallery.css';
import CloseIcon from '@material-ui/icons/Close';

const Gallery = () => {
  let data = [
    {
      id:1,
      imgSrc: "img/1.jpg",
    },
    {
      id:2,
      imgSrc: "img/2.jpg",
    },
    {
      id:3,
      imgSrc: "img/3.jpg",
    },
    {
      id:4,
      imgSrc: "img/4.jpg",
    },
    {
      id:5,
      imgSrc: "img/5.jpg",
    },
    {
      id:6,
      imgSrc: "img/206.jpg",
    },
    {
      id:7,
      imgSrc: "img/213.jpg",
    },
    {
      id:8,
      imgSrc: "img/213side.jpg",
    },
    // {
    //   id:9,
    //   imgSrc: "img/cat.jpg",
    // },
    // {
    //   id:10,
    //   imgSrc: "img/hnv.jpg",
    // },
    // {
    //   id:11,
    //   imgSrc: "img/hun.jpg",
    // },
    // {
    //   id:12,
    //   imgSrc: "img/king.jpg",
    // },
    // {
    //   id:13,
    //   imgSrc: "img/mook.jpg",
    // },
    // {
    //   id:14,
    //   imgSrc: "img/one.jpg",
    // },
    // {
    //   id:15,
    //   imgSrc: "img/Pro.jpg",
    // },
    // {
    //   id:16,
    //   imgSrc: "img/seunghwan.jpg",
    // },
    // {
    //   id:17,
    //   imgSrc: "img/summer.jpg",
    // },
    // {
    //   id:18,
    //   imgSrc: "img/una.jpg",
    // },
    // {
    //   id:19,
    //   imgSrc: "img/woon.jpg",
    // }
  ]
  const [model, setModel] = useState(false);
  const[tempimgSrc, setTempImgSrc] = useState('');

  const getImg = (imgSrc) =>{
    setTempImgSrc(imgSrc);
    setModel(true);
  }
  return(
    <>
    <div className={model? "model open" : "model"}>
      <img src={tempimgSrc} />
      <CloseIcon onClick={() => setModel(false)} />
    </div>
    <div className="gallery">
      {data.map((item, index)=>{
        return(
          <div className="pics" key={index} onClick={()=>getImg(item.imgSrc)}>
            <img src={item.imgSrc} style={{width: '100%'}} />
          </div>
        )
      })}
    </div>
    </>
  )
}

export default Gallery;